
import React, {useState, useRef} from 'react';
import JoditEditor from "jodit-react";

interface EditorProps {    
    
}

const Editor: React.FC<EditorProps> = (props) => {

    const editor = useRef(null)
	const [content, setContent] = useState('<p>rrwrw</p><p><strong><br></strong></p><p><strong>fsf</strong></p>')
	
	const config = {
		readonly: false, // all options from https://xdsoft.net/jodit/doc/
        height: 500
	}

    const clickEvnet = () => {
        console.info('You clicked content', content);
      };
    return (
        <div>        
             <div className="common-button__group--add" 
             onClick={() => clickEvnet()}>신규그룹 +</div>   

            {/* https://github.com/jodit/jodit-react */}
            <JoditEditor
            	ref={editor}
                value={content}
                config={config}
                // tabIndex={1} 
                onBlur={newContent => setContent(newContent)} 
                onChange={newContent => {}}
            />
           
        </div>
    )

}
export default  Editor