import * as React from 'react';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import CommentIcon from '@mui/icons-material/Comment';
import IconButton from '@mui/material/IconButton';
import Divider from '@mui/material/Divider';
import AddBoxIcon from '@mui/icons-material/AddBox';
import IndeterminateCheckBoxIcon from '@mui/icons-material/IndeterminateCheckBox';
import Input from '@mui/material/Input';
import FormControl from '@mui/material/FormControl';
import { InputAdornment, InputBase } from '@mui/material';
import { useState, useRef } from 'react';
import TextField from '@mui/material/TextField';

interface ListsProps {

}

const Lists: React.FC<ListsProps> = (props) => {
    type ListsArrayProps = { id: number; text: string };
    const initList = {
        id: 0,
        text: 'Init Title',
    }
    const [lists, setLists] = useState<ListsArrayProps[]>([initList]);
    const [errorMessage, setErrorMessage] = React.useState("");

    const nextId = useRef(0);
    const [inputText, setInputText] = useState("");

    const onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        setInputText(e.target.value);
        setErrorMessage("");
    }
    const clickAddEvent = () => {
        if (inputText == "") {
            setErrorMessage(
                "문항을 입력해주세요!"
            );
        } else {
            const id = nextId.current += 1;
            const list = {
                id: nextId.current,
                text: inputText,
            };
            setLists(lists.concat(list));
        }

    };
    const clickDeleteEvent = (itemId: number) => {
        console.info('You clicked clickDeleteEvent.');
        setLists(lists.filter(list => list.id !== itemId));
    };

    return (
        <>
            <List sx={{ width: '100%', bgcolor: 'transparent' }} disablePadding>
                {lists.map((item, index) => (
                    <ListItem
                        disableGutters={true}
                        key={item.id}
                        secondaryAction={
                            <IconButton>
                                {
                                    item.id === 0 ?
                                        <AddBoxIcon onClick={() => clickAddEvent()} />
                                        :
                                        <IndeterminateCheckBoxIcon onClick={() => clickDeleteEvent(item.id)} />
                                }
                            </IconButton>
                        }
                        divider={true}
                    >
                        {
                            item.id === 0 ?
                                // <InputBase
                                //     name="text"
                                //     sx={{ ml: 1, flex: 1 }}
                                //     size="small"
                                //     placeholder="아이가 이름을 부르면 반응을 하나요?"
                                //     inputProps={{ 'aria-label': 'search google maps' }}
                                //     onChange={onChange}
                                // />

                                <TextField
                                    error={inputText.length == 0}
                                    name="text"
                                    size="small"
                                    sx={{ ml: 1, flex: 1 }}
                                    id="outlined-error"
                                    label="입력하기"
                                    variant="standard"
                                    onChange={onChange}
                                    value={inputText}
                                    helperText={errorMessage}

                                    InputProps={{
                                        disableUnderline: true,
                                        startAdornment: (
                                            <InputAdornment position="start">
                                                <img src="/asset/img/treatment_icon_pencil.svg" />
                                            </InputAdornment>
                                        )
                                    }}
                                />

                                :
                                <ListItemText primary={item.text} />
                        }
                    </ListItem>
                ))}
            </List>
        </>
    )
}
export default Lists