import axios, { AxiosResponse } from 'axios';
import { PostType } from './post.interface';
import Cookies from 'universal-cookie';

// https://ichi.pro/ko/typescript-mich-axiosleul-sayonghan-peuleonteu-endeu-api-hochul-114869657297858

const cookie = new Cookies();

const ACCESS_TOKEN = cookie.get('access_token');
const XSRF_TOKEN = cookie.get('XSRF-TOKEN');

const instance = axios.create({
	baseURL: 'http://jsonplaceholder.typicode.com/',
	timeout: 15000,
});
instance.defaults.headers.common['Authorization'] = `Bearer ${ACCESS_TOKEN}`;
instance.defaults.headers.common['Content-Type'] = 'application/json';
instance.defaults.headers.common['X-XSRF-TOKEN'] = XSRF_TOKEN;

instance.interceptors.request.use(async (config) => {
    try{
        document.body.classList.add('loading-indicator');
    }
    catch(e)
    {
        alert('Error request' + e);
    }
    return config;
});

instance.interceptors.response.use(async (config) => {
    try {
        document.body.classList.remove('loading-indicator');
    }
    catch(e)
    {
        alert('Error response' + e);   
    }
    return config;
});


const responseBody = (response: AxiosResponse) => response.data;

const requests = {
	get: (url: string) => instance.get(url).then(responseBody),
	post: (url: string, body: {}) => instance.post(url, body).then(responseBody),
	put: (url: string, body: {}) => instance.put(url, body).then(responseBody),
	delete: (url: string) => instance.delete(url).then(responseBody),
};

export const Post = {
	getPosts: (): Promise<PostType[]> => requests.get('posts'),
	getAPost: (id: number): Promise<PostType> => requests.get(`posts/${id}`),
	createPost: (post: PostType): Promise<PostType> => requests.post('posts', post),
	updatePost: (post: PostType, id: number): Promise<PostType> => requests.put(`posts/${id}`, post),
	deletePost: (id: number): Promise<void> => requests.delete(`posts/${id}`),
};