// 네모버튼 로그인 / 다음 / 이전 / 보호자입니다....
import React from "react";
import styled from "styled-components";

interface ButtonSquareProps {
    children?: React.ReactNode;
    width?: number;
    background?: string;
    minWidth?: number;
    type?: number;
    onClick?: () => void;
}

const ButtonSquareLineB: React.FC<ButtonSquareProps> = (props) => {
    const { width, children, background, minWidth, type, onClick } = props;

    const Styled0 = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2px 10px 2px 10px;
    margin: 1px 2px 1px 2px ;
    height:27px;
    min-width:${minWidth}px;
    
   
    font-family: "Elice Digital Baeum",sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    text-align: center;
    letter-spacing: 0.03em;
    color: #573300;
    border: 1px solid #573300;
    border-radius: 5px;
    background: "#FFFFFF";

    &:active,
    &:hover{
      background: #573300;
      color: #FFFFFF;
      cursor: pointer;
    }
    &:focus {
      background: var(--button-hover-bg-color, #FFFFFF);
      cursor: pointer;
    }

    &:disabled {
      cursor: default;
      opacity: 0.5;
      background: var(--button-bg-color, #FFFFFF);
    }
  `;

    const Styled1 = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 10px 10px 10px;
    margin: 10px 20px 10px 20px ;
    min-width:${minWidth}px;
    
   
    font-family: "Elice Digital Baeum",sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    text-align: center;
    letter-spacing: 0.03em;
    color: #FFFFFF;
    border: 1px solid #573300;
    border-radius: 5px;
    background: #573300;

    &:active,
    &:hover{
      background: #FFFFFF;
      color: #573300;
      cursor: pointer;
    }
    &:focus {
      background: var(--button-hover-bg-color, #FFFFFF);
      cursor: pointer;
    }

    &:disabled {
      cursor: default;
      opacity: 0.5;
      background: var(--button-bg-color, #FFFFFF);
    }
  `;

    return (
        <>
            {
                type === 0 ?
                    <Styled0
                        style={{
                            width: `${width}px`,
                        }}
                        onClick={onClick}
                    >
                        {children}
                    </Styled0>
                    :
                    <Styled1
                        style={{
                            width: `${width}px`,
                        }}
                        onClick={onClick}
                    >
                        {children}
                    </Styled1>

            }


        </>

    );
};

ButtonSquareLineB.defaultProps = {
    minWidth: 50,
    type: 0,
}

export default ButtonSquareLineB

