import { Container, Stepper, StepLabel, Step, Grid, InputBase } from "@mui/material"
import { useState, useEffect } from "react";
import { useRecoilState, useRecoilValue, useSetRecoilState } from "recoil";
import { Footer, Header } from "../components"
import ButtonSquare from "../components/ButtonSquare"
import ButtonSquareLarge from "../components/ButtonSquareLarge";
import SignUpForm from "../components/SignUpForm";
import StepOne from "../img/step1.svg"
import StepTwo from "../img/step2.svg"
import StepThr from "../img/step3.svg"
import StepFor from "../img/step4.svg"
import { countState } from "../service/atom";


interface SignUpProps {

}

const SignUp: React.FC<SignUpProps> = (props) => {

  const [count, setCount] = useRecoilState(countState); // useRecoilState 을 통한 value, setter 반환
  const setCountUseSetRecoilState = useSetRecoilState(countState); // 값을 변경하는 함수만 반환
  const countVar = useRecoilValue(countState);
  useEffect(() => {
    setCountUseSetRecoilState(count + 1);
  }, []);


  const [step, setStep] = useState(1);
  let imgComponent = null;
  let component = null;
  const clickEvnet = (no: number) => {
    setStep(no);
    setCountUseSetRecoilState(count + 1);
  };
  const linkSignIn = () => {
    window.location.href = "/signin";
  };

  const [menu, setMenu] = useState(1);
  const [menuArray, setMenuArray] = useState([0, 0, 0]);
  const changeMenu = (num: number) => {
    setMenu(() => num);
    console.log("menu", menu);
    const newArr = [...menuArray];
    menuArray.map((item: number, index: number) => {
      if (num == index) {
        newArr[index] = 1;
      } else {
        newArr[index] = 0;
      }
      setMenuArray(() => newArr);
    }
    );
  }
  const clickMenu = (num: number) => {
    changeMenu(num);
  };

  const [menu2, setMenu2] = useState(1);
  const [menuArray2, setMenuArray2] = useState([0, 0, 0]);
  const changeMenu2 = (num: number) => {
    setMenu2(() => num);
    console.log("menu2", menu2);
    const newArr2 = [...menuArray2];
    menuArray2.map((item: number, index: number) => {
      if (num == index) {
        newArr2[index] = 1;
      } else {
        newArr2[index] = 0;
      }
      setMenuArray2(() => newArr2);
    }
    );
  }
  const clickMenu2 = (num: number) => {
    changeMenu2(num);
  };


  switch (step) {
    case 1:
      imgComponent = <img src={StepOne} />
      component = (<>
        <div style={{ paddingLeft: 20, paddingRight: 20 }}>
          {countVar}
          <ButtonSquareLarge background="#FF9100" onClick={() => clickMenu(1)} type={menuArray[1]}>보호자입니다.</ButtonSquareLarge>
        </div>
        <div style={{ paddingLeft: 20, paddingRight: 20 }}>
          <ButtonSquareLarge background="#00CDB6" onClick={() => clickMenu(2)} type={menuArray[2]}>치료사입니다.</ButtonSquareLarge>
        </div>
        <div style={{ paddingLeft: 20, paddingRight: 20 }}>
          <ButtonSquareLarge onClick={() => clickEvnet(2)}>다음</ButtonSquareLarge>
        </div>
        <div style={{ paddingLeft: 20, paddingRight: 20 }}>
          <ButtonSquareLarge onClick={() => linkSignIn()}>로그인</ButtonSquareLarge>
        </div>
      </>
      )
      break;
    case 2:
      imgComponent = <img src={StepTwo} />
      component = (<>
        <div style={{ paddingLeft: 20, paddingRight: 20 }}>
          {countVar}
          <ButtonSquareLarge background="#FF9100" onClick={() => clickMenu2(1)} type={menuArray2[1]}>기관/단체입니다.</ButtonSquareLarge>
        </div>
        <div style={{ paddingLeft: 20, paddingRight: 20 }}>
          <ButtonSquareLarge background="#00CDB6" onClick={() => clickMenu2(2)} type={menuArray2[2]}>개인입니다.</ButtonSquareLarge>
        </div>
        <div style={{ paddingLeft: 20, paddingRight: 20 }}>
          <ButtonSquareLarge onClick={() => clickEvnet(3)}>다음</ButtonSquareLarge>
        </div>
        <div style={{ paddingLeft: 20, paddingRight: 20 }}>
          <ButtonSquareLarge onClick={() => clickEvnet(1)}>이전</ButtonSquareLarge>
        </div>
      </>
      )
      break;
    case 3:
      imgComponent = <img src={StepThr} />
      component = (<>
        <SignUpForm clickEvnet={clickEvnet}></SignUpForm>
        {/* <div style={{ paddingLeft: 20, paddingRight: 20 }}>
          <ButtonSquareLarge onClick={() => clickEvnet(4)}>다음</ButtonSquareLarge>
        </div>
        <div style={{ paddingLeft: 20, paddingRight: 20 }}>
          <ButtonSquareLarge onClick={() => clickEvnet(2)}>이전</ButtonSquareLarge>
        </div> */}
      </>
      )
      break;
    case 4:
      imgComponent = <img src={StepFor} />
      component = (<><div className="common-flex-row-wrap-hcenter-vcenter">
        <span className="font-bold-s17-c573300" style={{ textAlign: "center", marginTop: 30 }}>
          반갑습니다 선생님!<br /></span>
        <span className="font-normal-s17-c573300"
          style={{ textAlign: "center", marginLeft: 35, marginRight: 35, marginBottom: 30 }}>
          지금 로그인하여 굿로드 서비스를 이용해보세요!<br />
          보다 좋은 소비스를 위해 추가 인증을 요구할 수 있습니다<br />
          추가 인증이 필요한 경우 이메일로 별도 안내 드리도록 하겠습니다.</span> </div>
        <div style={{ paddingLeft: 20, paddingRight: 20 }}>
          <ButtonSquareLarge onClick={() => linkSignIn()}>로그인</ButtonSquareLarge>
        </div>

      </>
      )
      break;
  }




  return (
    <div className="common-bg-login">
      <Header
        className=""
        goodroad="/asset/img/main_top_logo.svg"
        person="/asset/img/non_member_icon_man.svg"
        bell="/asset/img/main_Icon_awesome-bell.svg"
        kakao="/asset/img/main_icon_message.svg"
        color="font-bold-s12-c573300"
      />
      <div className="media-wrapper">
        <div className="common-wrapper__container"></div>
        <div className="common-wrapper__container">
          {/* ######################### Content Area Begin ######################### */}
          <div className="media-blank-50"></div>
          <div className="common-row-wrap-hcenter-vstretch" style={{ margin: 10 }}>
            <div className="media-sign-container">
              <div className="media-blank-50"></div>
              <div className="common-flex-column-wrap-hcenter-vcenter">
                <span className="sign-in-up-good-load">GOOD ROAD</span>
                <div style={{ marginTop: 10, marginBottom: 10 }}>{imgComponent}</div>
              </div>
              {component}
            </div>
          </div>
          <div className="media-blank-50"></div>

          {/* ######################### Content Area End   ######################### */}
        </div>
        <div className="common-wrapper__container"></div>
        {/* <Footer></Footer> */}
      </div>
    </div>
  )

}

export default SignUp