// 네모버튼 로그인 / 다음 / 이전 / 보호자입니다....
import React from "react";
import styled from "styled-components";

interface ButtonSquareProps {
  children?: React.ReactNode;
  width?: number;
  background?: string;
  minWidth?: number;
  marginLeft?: number;
  marginRight?: number;
  onClick?: () => void;
}

const ButtonSquare: React.FC<ButtonSquareProps> = (props) => {
  const { width, children, background, minWidth, marginLeft, marginRight, onClick } = props;

  const Styled = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 10px 10px 10px;
    margin: 10px ${marginRight}px 10px ${marginLeft}px ;
    min-width:${minWidth}px;
    
   
    font-family: "Elice Digital Baeum",sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    text-align: center;
    letter-spacing: 0.03em;
    color: #FFFFFF;
    background: #573300;
    border-radius: 5px;

    &:active,
    &:hover{
      background: #AB9980;
      color: #000000;
      cursor: pointer;
    }
    &:focus {
      background: var(--button-hover-bg-color, #FFFFFF);
      cursor: pointer;
    }

    &:disabled {
      cursor: default;
      opacity: 0.5;
      background: var(--button-bg-color, #FFFFFF);
    }
  `;

  return (
    <Styled
      style={{
        width: `${width}px`,
        // background: `${background}`,
      }}
      onClick={onClick}
    >
      {children}
    </Styled>
  );
};

ButtonSquare.defaultProps = {
  background: "#573300",
  minWidth: 260,
  marginLeft: 0,
  marginRight: 0
}

export default ButtonSquare

