function valuetext(value: number) {
  return `${value}세`;
}
interface AirbnbThumbComponentProps extends React.HTMLAttributes<unknown> { }



function AirbnbThumbComponent(props: AirbnbThumbComponentProps) {
  const { children, className, ...other } = props;

  const extraClassName = other["aria-level"] === 0 ? "first-thumb" : "second-thumb";

  console.log("other", other["defaultValue"]?.toString);
  console.log("className", className);


  return (

    <SliderThumb {...other} className={clsx(className, extraClassName)}>
      {children}

      <span className="rc-slider-handle-l" />

      {/* <span className="airbnb-bar" />
      <span className="airbnb-bar" />
      <span className="airbnb-bar" /> */}
    </SliderThumb>

  );
}

AirbnbThumbComponent.propTypes = {
  children: PropTypes.node
};



export default function SliderTest() {
  const [value, setValue] = React.useState<number[]>([2, 6]);
  const handleChange = (event: Event, newValue: number | number[]) => {
    //console.log("handleChange" , value[0]);
    setValue(newValue as number[]);
  };



  return (
    <>
      <AirbnbSlider
        components={{ Thumb: AirbnbThumbComponent }}
        getAriaLabel={(index) =>
          index === 0 ? "Minimum price" : "Maximum price"
        }
        marks={marks}
        value={value}
        onChange={handleChange}
        valueLabelDisplay="auto"
        getAriaValueText={valuetext}
        min={0} max={16}
      />
    </>

  );
}